import React, { useState } from 'react'
import { Box, Button, Grid, MenuItem, Popover, Stack, Typography } from '@mui/material'
import { cache } from '../../utils/cache'
import { useTranslation } from 'react-i18next'
import { useNavigate } from 'react-router-dom'
import { config_path, portalCfg } from '../../router/config.path'
import PublicPage from '../PublicPage'
import Modal from '../../components/Modal';
export default function LandingPageView(props) {
    const appConfig = cache.getAppConfig();
    const { t } = useTranslation();
    const navigate = useNavigate();

    const {defaultLanguage, languages} = props;
    const [anchorElLanguage, setAnchorElLanguage] = useState(null);

    const handleOpenLanguages = (event) => {
        setAnchorElLanguage(event.currentTarget);
    };

    const onChangeLanguage = async (item) => {
        setAnchorElLanguage(null);
        props.onChangeLanguage(item);
    }

    console.log("props.showRegisterAction:",props.showRegisterAction);
    return (
        <PublicPage confirm={props.confirm}>
            <Grid item xs={12} marginTop={2}>
                <Typography variant='h5' textAlign={'center'} my={6}>{t('landing_welcome')} {appConfig.name}</Typography>
            </Grid>
            <Grid item xs={12}>
                <Grid item xs={12} marginTop={10}>
                    <Button size='large' id='landingPage/btnLogin' fullWidth variant='contained' onClick={() => navigate(config_path.login)}>{t('btn_login')}</Button>
                </Grid>
                <Grid item xs={12} marginTop={2}>
                    <Button size='large' id='landingPage/btnRegister' fullWidth variant='outlined' onClick={() => props.setShowRegisterAction(true)}>{t('btn_register')}</Button>
                </Grid>
                {/* <Grid item xs={12} container justifyContent={'center'} marginTop={7}>
                    <Typography variant='subtitle1'>{'© CRM.COM'}</Typography>
                </Grid> */}
            </Grid>
            <Grid item xs={12} container sx={{
                position: 'absolute',
                bottom: 8,
                width: 'calc(100% - 48px)',
                justifyContent: 'center',
                alignItems: 'center'
            }} >
                <Button onClick={(e) => handleOpenLanguages(e)} size='large' id='header/changeLanguage'>
                    {defaultLanguage ? defaultLanguage.label : ""}
                </Button>
                <Grid xs={12} item container justifyContent={'center'}>
                    <Typography variant='subtitle1'>{'© CRM.COM'}</Typography>
                </Grid>
            </Grid>

            <Popover id='list-languages'
                anchorEl={anchorElLanguage}
                open={Boolean(anchorElLanguage)}
                onClose={() => setAnchorElLanguage(null)}
                sx={{
                    top: 45,
                }}
            >
                {languages && languages.length > 0 && languages.map((item, index) => {
                    return (
                        <MenuItem key={index} onClick={() => onChangeLanguage(item)}>
                            <Typography variant='subtitle2'>{item.label}</Typography>
                        </MenuItem>
                    )
                })}
            </Popover>
            {props.showRegisterAction && <Modal
                isOpen={props.showRegisterAction}
                onClose={() => props.setShowRegisterAction(false)}
            >
                <Box>
                    <Stack direction={'row'}>
                        <Typography>{t('exits_rewards_participant')}</Typography>
                    </Stack>
                    <Stack marginTop={4} direction={'row'} justifyContent={'center'} alignItems={'center'}>
                        <Stack flex={1} justifyContent={'center'} alignItems={'center'} spacing={2}>
                            <img src={portalCfg.publicPath + '/assets/images/register_have_card.png'} alt={'Logo Image'} height={100} />
                            <Button id='landingPage/btnRegisterHaveCard' onClick={() => navigate(config_path.register_existing_customer)} variant='contained'>{t('btn_register_have_card')}</Button>
                        </Stack>
                        <Stack flex={1} justifyContent={'center'} alignItems={'center'} spacing={2}>
                            <img src={portalCfg.publicPath + '/assets/images/register_new.png'} alt={'Logo Image'} height={100} />
                            <Button id='landingPage/btnRegisterNew' onClick={() => navigate(config_path.register)}  variant='contained'>{t('btn_register_new')}</Button>
                        </Stack>
                    </Stack>
                </Box>
            </Modal>}
        </PublicPage>
    )
}
