import React, { useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom'
import { portal_config } from '../../portal.config';
import { config_path } from '../../router/config.path';
import LandingPageView from './LandingPageView'
import { getLanguage, storeLanguageAsync } from '../../utils/common';
import { cache } from '../../utils/cache';
import { custom_page } from '../../custom.config';

const LandingPage = custom_page.lading_page && custom_page.lading_page.view ? require('../../custom_src/pages/landing-page/LandingPageView').default : LandingPageView;

export default function LandingPageController(props) {
    const navigate = useNavigate();
    const { t } = useTranslation();

    const [confirm, setConfirm] = useState({});
    const [showRegisterAction,setShowRegisterAction] = useState(false);
    const [currentLanguage, setLanguage] = useState(getLanguage());
    const [defaultLanguage, setDefaultLanguage] = useState(null);
    const languages = cache.getLanguages();

    useEffect(() => {
        if (currentLanguage) {
            console.log('languages:',languages);
            let exLanguage = languages.filter(x => x.value == currentLanguage);
            console.log("exLanguage:",exLanguage);
            if (exLanguage && exLanguage.length > 0){
                setDefaultLanguage(exLanguage[0]);
            } 
        }
    }, [])

    const onRegister = () => {
        if(!portal_config.isDisableRegisterExisingCustomer) {
            setConfirm({
                show: true,
                message: t('exits_rewards_participant'),
                actionTitle: t('btn_register_have_card'),
                closeTitle: t('btn_register_new'),
                onAction: () => {
                    navigate(config_path.register_existing_customer);
                },
                onClose: () => {
                    navigate(config_path.register)
                },
            })
        } else {
            navigate(config_path.register);
        }
    }

    const onChangeLanguage = async (item) => {
        let _language = languages.filter(x => x.value == item.value);
        if(_language && _language.length > 0){
            setLanguage(_language[0].value);
            storeLanguageAsync(item.value);
            setDefaultLanguage(_language[0]);
        }
    }


    return (
        <LandingPage 
            confirm={confirm}
            showRegisterAction={showRegisterAction}
            languages={languages}
            defaultLanguage={defaultLanguage}
            setShowRegisterAction={setShowRegisterAction}
            onChangeLanguage={onChangeLanguage}
        />
    )
}
